x;\r\n    }\r\n  }\r\n\r\n  &__counter-container {\r\n    position: relative;\r\n  }\r\n}",".drop-with-bullet {\r\n  width: 100%;\r\n\r\n  &__title-wrap {\r\n    margin-bottom: 5px;\r\n    display: flex;\r\n    justify-content: space-between;\r\n\r\n    &_with-bottom-gap {\r\n      margin-bottom: 16px;\r\n    }\r\n  }\r\n\r\n  &__input {\r\n    margin-bottom: 20px;\r\n  }\r\n\r\n  &__counter-container {\r\n    position: relative;\r\n  }\r\n}",".input {\r\n  display: flex;\r\n  width: 100%;\r\n\r\n  &:hover {\r\n    .input__base {\r\n      border-color: $darkShade75;\r\n    }\r\n\r\n    .input__button {\r\n      border-color: $darkShade75;\r\n    }\r\n  }\r\n\r\n  &__base {\r\n    padding: 9px 14px;\r\n    box-sizing: border-box;\r\n    width: 100%;\r\n    font-family: $font-family;\r\n    font-size: 14px;\r\n    line-height: 24px;\r\n    color: $darkShade75;\r\n    border: 1px solid $darkShade25;\r\n    border-radius: 4px;\r\n    outline: none;\r\n    transition: border .2s ease;\r\n    appearance: none;\r\n    \r\n    &::placeholder {\r\n      color: $darkShade25;\r\n      transition: color .2s ease;\r\n    }\r\n    \r\n    &:hover,\r\n    &:focus {\r\n      border-color: $darkShade75;\r\n\r\n      + .input__button {\r\n        border-color: $darkShade75;\r\n      }\r\n    }\r\n\r\n    &_with-button {\r\n      padding-right: 0;\r\n      border-right: none;\r\n      border-radius: 4px 0 0 4px;\r\n    }\r\n\r\n    &_focused {\r\n      border-color: $darkShade75;\r\n\r\n      &::placeholder {\r\n        color: $darkShade75;\r\n      }\r\n    }\r\n\r\n  }\r\n\r\n  &__button {\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    width: 44px;\r\n    height: 44px;\r\n    background-color: transparent;\r\n    background-image: url('./img/arrow.svg');\r\n    background-repeat: no-repeat;\r\n    background-position: center center;\r\n    border: 1px solid $darkShade25;\r\n    border-left: none;\r\n    border-radius: 0 4px 4px 0;\r\n    outline: none;\r\n    transition: border .2s ease, background .1s ease;\r\n    cursor: pointer;\r\n  }\r\n}",".typography {\r\n  margin: 0;\r\n\r\n  &_with-variant {\r\n    &-h1,\r\n    &-h2 {\r\n      font-family: $font-family;\r\n      font-weight: bold;\r\n    }\r\n\r\n    &-h1 {\r\n      font-size: 24px;\r\n      line-height: 30px;\r\n    }\r\n\r\n    &-h2 {\r\n      font-size: 19px;\r\n      line-height: 24px;\r\n    }\r\n\r\n    &-h3 {\r\n      font-family: $font-family;\r\n      font-size: 12px;\r\n      line-height: 15px;\r\n      font-weight: bold;\r\n    }\r\n\r\n    &-body {\r\n      font-family: $font-family;\r\n      font-size: 14px;\r\n      line-height: 24px;\r\n      font-weight: normal;\r\n      color: $darkShade75;;\r\n    }\r\n  }\r\n\r\n  &_upper {\r\n    text-transform: uppercase;\r\n  }\r\n}",".drop-down-input {\r\n  &__wrap {\r\n    display: flex;\r\n    width: 100%;\r\n    min-width: 150px;\r\n    user-select: none;\r\n\r\n    &:active {\r\n      .drop-down-input__button {\r\n        background-position: center 60%;\r\n      }\r\n    }\r\n  }\r\n  \r\n  &__body {\r\n    padding: 9px 14px;\r\n    padding-right: 0;\r\n    box-sizing: border-box;\r\n    width: 100%;\r\n    font-family: $font-family;\r\n    font-size: 14px;\r\n    line-height: 24px;\r\n    color: $darkShade75;\r\n    border: 1px solid $darkShade25;\r\n    border-right: none;\r\n    border-radius: 4px 0 0 4px;\r\n    outline: none;\r\n    transition: border 0.2s ease;\r\n    cursor: pointer;\r\n  }\r\n\r\n  &__button {\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    width: 44px;\r\n    height: 44px;\r\n    background-color: transparent;\r\n    background-image: url('./img/expand_more.svg');\r\n    background-repeat: no-repeat;\r\n    background-position: center center;\r\n    border: 1px solid $darkShade25;\r\n    border-left: none;\r\n    border-radius: 0 4px 4px 0;\r\n    outline: none;\r\n    transition: all 0.2s ease;\r\n    cursor: pointer;\r\n\r\n    &:active{\r\n      background-position: center 60%